const Email = () => {
    return (
        <React.Fragment>
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
            >
                <path fill="none" d="M0 0h24v24H0V0z" />
                <path d="M22 6c0-1.1-.9-2-2-2H4c-1.1 0-2 .9-2 2v12c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V6zm-2 0l-8 5-8-5h16zm0 12H4V8l8 5 8-5v10z" />
            </svg>
        </React.Fragment>
    )
}

export default class Footer extends Component {
    render() {
        return (
            <div id="footer">
                <div className="container">
                    <div className="footer-container">
                        <ul className="links">
                            <li>
                                <Link to="/projects">Projects</Link>
                            </li>
                            <li>
                                <Link to="/gallery">Gallery</Link>
                            </li>
                            <li>
                                <Link to="/resources">Resources</Link>
                            </li>
                            <li>
                                <Link to="/teams">Teams</Link>
                            </li>
                            <li>
                                <Link to="/about">About</Link>
                            </li>
                        </ul>
                        <div className="footer-contact">
                            <h2>Contact Us</h2>
                            <ul>
                                <li>
                                    <Place />
                                    <span>Adamas Knowledge City</span>
                                </li>
                                <li
                                    style={{
                                        marginLeft: '30px'
                                    }}
                                >
                                    <span>
                                        Barasat-Barrackpore road, Kolkata-700
                                        126
                                    </span>
                                </li>
                                <li>
                                    <Email />
                                    <span>adamasrobozoid123@gmail.com</span>
                                </li>
                            </ul>
                            <div className="social_links">
                                <h2>Follow us on: </h2>
                                <br />
                                <a href="https://www.facebook.com/adamasrobozoid/">
                                    <i className="fab fa-facebook-square" />
                                </a>
                                <a href="https://www.youtube.com/channel/UCxBxqEiSrrv6wwPp8t2I4qQ">
                                    <i className="fab fa-youtube" />
                                </a>
                            </div>
                        </div>
                    </div>
                    <h1>© 2018 Adamas Robozoid, Adamas University</h1>
                </div>
            </div>
        )
    }
}
